@charset "UTF-8";
:root {
  --content-width: 86.25rem;
  --container-offset: 1rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --font-babas: "BebasNeue", sans-serif;
  --font-pt: "PTSansCaption", sans-serif;
  --light-color: #fff;
  --silver-color: #b4b4b4;
  --color-red: #cd313c;
  --color-black: #202020;
  --color-blue: #0073b4;
  --color-green: #3ca037;
}

/* stylelint-disable */
@font-face {
  font-family: "BebasNeue";
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "BebasNeue";
  src: url("../fonts/BebasNeueBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
@font-face {
  font-family: "PTSansCaption";
  src: url("../fonts/PTSansCaption-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PTSansCaption";
  src: url("../fonts/PTSansCaption-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}
html * {
  scroll-margin: 5.1875rem;
}

body {
  font-family: var(--font-babas);
  line-height: 1;
}
body.dis-scroll {
  overflow: hidden;
}

.main {
  background-color: rgb(242, 242, 242);
  padding-bottom: 7.5rem;
}
@media (max-width: 480px) {
  .main {
    padding-bottom: 5rem;
  }
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

li {
  list-style: none;
}

input {
  border: none;
}

main {
  flex-grow: 1;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.text_red {
  color: var(--color-red);
}

.subtitle {
  font-size: 6rem;
  font-weight: 700;
}
@media (max-width: 768px) {
  .subtitle {
    font-size: 3rem;
  }
}

.button {
  display: inline-block;
  padding: 1rem 1.5rem;
  color: var(--light-color);
  font-family: var(--font-babas);
  font-weight: 700;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  transition: background-color 0.2s ease-in-out;
}
.button_red {
  background-color: var(--color-red);
}
.button_red:hover {
  background-color: #c02c36;
}
.button_red:active {
  background-color: #ac1b25;
}
.button_border {
  border: 0.125rem solid var(--color-red);
  color: var(--color-red);
}
.button_border:hover {
  background-color: rgba(205, 49, 60, 0.05);
}

.accordion {
  margin-top: 3.5rem;
}
@media (max-width: 480px) {
  .accordion {
    margin-top: 2rem;
  }
}

.accordion-item {
  padding: 1.5rem 0;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
}
@media (max-width: 480px) {
  .accordion-item {
    padding: 1rem 0;
  }
}

.accordion-header {
  text-align: left;
  width: 100%;
  font-size: 2.25rem;
  font-weight: 700;
  color: var(--color-black);
  display: flex;
  justify-content: space-between;
}
@media (max-width: 480px) {
  .accordion-header {
    font-size: 1.5rem;
    gap: 1rem;
  }
}
.accordion-header_active {
  color: var(--color-red);
}
.accordion-header_active .accordion-header__arrow-icon {
  transform: rotate(90deg) scale(1.4);
}

.accordion-header__arrow {
  flex-shrink: 0;
  min-width: 2rem;
}
@media (max-width: 480px) {
  .accordion-header__arrow {
    width: 1rem;
    height: 1rem;
  }
}
.accordion-header__arrow img {
  display: block;
}

.accordion-header__arrow-icon {
  transform: scale(1.4);
  transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}
.accordion-content > *:first-child {
  padding-top: 1.5rem;
}
@media (max-width: 480px) {
  .accordion-content > *:first-child {
    padding-top: 1rem;
  }
}

.accordion-text {
  font-size: 1.125rem;
  font-family: var(--font-pt);
}
@media (max-width: 480px) {
  .accordion-text {
    font-size: 1rem;
  }
}
.accordion-text + .accordion-text {
  margin-top: 0.75rem;
}

.footer {
  position: relative;
  background-color: #d6d9de;
}

.footer__top {
  padding-top: 1.5rem;
  display: flex;
  gap: 5rem;
}
@media (max-width: 1280px) {
  .footer__top {
    gap: 1rem;
  }
}
@media (max-width: 1023px) {
  .footer__top {
    display: block;
  }
}
@media (max-width: 480px) {
  .footer__top {
    padding-top: 2.5rem;
  }
}

.footer__organization {
  font-size: 1.25rem;
  font-weight: 700;
  font-family: var(--font-pt);
  max-width: 23.4375rem;
}
@media (max-width: 1280px) {
  .footer__organization {
    max-width: 17.5rem;
  }
}

.footer__info {
  text-align: right;
  margin-left: auto;
  display: flex;
  gap: 5rem;
}
@media (max-width: 1280px) {
  .footer__info {
    gap: 1rem;
  }
}
@media (max-width: 1023px) {
  .footer__info {
    text-align: left;
    display: block;
    margin-top: 1.5rem;
  }
}

@media (max-width: 1023px) {
  .footer__phone {
    margin-top: 1rem;
  }
}

.footer__descr {
  font-family: var(--font-pt);
}

.footer__address {
  text-align: right;
}
@media (max-width: 1023px) {
  .footer__address {
    text-align: left;
    margin-top: 1rem;
  }
}

.footer__link,
.footer__address-link {
  display: block;
  font-weight: 700;
  font-size: 1.75rem;
  margin-top: 0.625rem;
  transition: color 0.3s ease;
}
.footer__link:hover,
.footer__address-link:hover {
  color: var(--color-red);
}

.footer__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
  border-top: 1px solid rgb(180, 180, 180);
  padding: 1.875rem 0;
}
@media (max-width: 1023px) {
  .footer__bottom {
    flex-direction: column-reverse;
    align-items: flex-start;
    gap: 1rem;
    justify-content: flex-start;
    padding-top: 1rem;
  }
}

.footer__copy {
  font-family: var(--font-pt);
}

.footer__socials_mobile {
  display: none;
}
@media (max-width: 1023px) {
  .footer__socials_mobile {
    display: block;
    margin-top: 1rem;
  }
}
@media (max-width: 1023px) {
  .footer__socials_desctop {
    display: none;
  }
}

.footer__socials-list {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.footer__socials-link {
  display: block;
  transition: transform 0.2s ease;
}
.footer__socials-link:hover {
  transform: scale(1.2);
}
.footer__socials-link img {
  display: block;
}

.footer__policy-link {
  font-family: var(--font-pt);
  transition: color 0.3s ease;
}
.footer__policy-link:hover {
  color: var(--color-red);
}

.footer__back-to-top {
  display: flex;
  align-items: center;
  justify-content: center;
  left: 7%;
  top: 1.5rem;
  position: absolute;
  border-radius: 100%;
  width: 5rem;
  height: 5rem;
  background-color: var(--light-color);
  transition: transform 0.2s ease;
}
@media (max-width: 1800px) {
  .footer__back-to-top {
    left: 1rem;
  }
}
@media (max-width: 1600px) {
  .footer__back-to-top {
    width: 2.5rem;
    height: 2.5rem;
    top: -3rem;
  }
}
@media (max-width: 1023px) {
  .footer__back-to-top {
    display: none;
  }
}
.footer__back-to-top:hover {
  transform: scale(1.1);
}

.header {
  position: sticky;
  top: 0;
  z-index: 10;
  transition: transform 0.3s ease, background-color 0.3s ease;
  background-color: var(--light-color);
}
.header_slide {
  transform: translateY(-6.875rem);
}
@media (max-width: 768px) {
  .header_desktop {
    display: none;
  }
}
.header_mobile {
  display: none;
}
@media (max-width: 768px) {
  .header_mobile {
    display: block;
  }
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 0.5rem;
}

.header__info {
  display: flex;
  align-items: flex-end;
  gap: 1rem;
}
@media (max-width: 1024px) {
  .header__info {
    flex-direction: column;
  }
}

.header__phone-label {
  font-family: var(--font-pt);
  color: var(--silver-color);
}

.header__phone-link {
  font-weight: 700;
  display: block;
  margin-top: 0.375rem;
  font-size: 2rem;
  color: var(--color-red);
  transition: color 0.2s ease-in-out;
}
@media (max-width: 1280px) {
  .header__phone-link {
    font-size: 1.5rem;
  }
}
.header__phone-link:hover {
  color: black;
}

.header__pictures {
  display: flex;
  align-items: center;
  gap: 2rem;
}
@media (max-width: 1024px) {
  .header__pictures {
    gap: 0.5rem;
  }
}

.header__profile {
  display: block;
  line-height: 1;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1.5rem;
}

.header__profile-icon {
  color: white;
  fill: white;
  stroke: white;
}

.header__socials {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.header__social {
  transform: scale(1);
  transition: transform 0.2s ease-in-out;
}
.header__social:hover {
  transform: scale(1.1);
}

.header__bottom {
  padding: 1.5rem 0;
}

.header__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}

.header__menu-link {
  display: block;
  font-weight: 700;
  font-family: var(--font-babas);
  font-size: 2rem;
  position: relative;
  transition: color 0.2s ease-in-out;
}
@media (max-width: 1024px) {
  .header__menu-link {
    font-size: 1.5rem;
  }
}
.header__menu-link::before {
  content: "";
  position: absolute;
  bottom: -25px;
  width: 100%;
  height: 1rem;
  background-color: var(--color-red);
  transform: translateY(-100%);
  border-radius: 0.25rem 0.25rem 0 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
  opacity: 0;
}
.header__menu-link:hover {
  color: var(--color-red);
}
.header__menu-link:hover::before {
  opacity: 1;
  transform: translateY(0);
}
.header__menu-link_active {
  color: var(--color-red);
}
.header__menu-link_active::before {
  opacity: 1;
  transform: translateY(0);
}

.header-mobile {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background-color: var(--light-color);
  border-bottom: 1px solid #d9d9d9;
}

.header-mobile__burger {
  position: relative;
  width: 2rem;
  height: 2rem;
}
.header-mobile__burger span {
  position: absolute;
  background-color: var(--color-black);
  width: 70%;
  height: 0.1875rem;
  border-radius: 2.5rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: top 0.3s ease, transform 0.3s ease, opacity 0.3s ease;
}
.header-mobile__burger span:first-child {
  top: 25%;
}
.header-mobile__burger span:last-child {
  top: 75%;
}
.header-mobile__burger_active span:first-child {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.header-mobile__burger_active span:nth-child(2) {
  top: 50%;
  opacity: 0;
  transform: translate(-50%, -50%) scale(0);
}
.header-mobile__burger_active span:last-child {
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.header-mobile__logos {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.header-mobile__profile {
  display: flex;
  align-items: center;
  padding: 0.4375rem;
}

.header-mobile__menu {
  overflow: auto;
  display: flex;
  flex-direction: column;
  padding-top: 3.875rem;
  position: fixed;
  inset: 0;
  z-index: 5;
  background-color: var(--light-color);
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
}
.header-mobile__menu_active {
  transform: translateX(0);
}

.header-mobile__menu-list {
  padding: 1.5rem 1rem;
}

.header-mobile__menu-item + .header-mobile__menu-item {
  margin-top: 1.5rem;
}

.header-mobile__menu-link {
  font-size: 3rem;
  font-weight: 700;
}

.header-mobile__footer {
  padding: 1.5rem 1rem;
}

.header-mobile__footer-cols {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  padding: 16px 0;
  border-top: 1px solid rgba(180, 180, 180, 0.2);
  border-bottom: 1px solid rgba(180, 180, 180, 0.2);
}

.header-mobile__footer-title {
  font-family: var(--font-pt);
  color: var(--silver-color);
}

.header-mobile__footer-link {
  display: block;
  font-size: 1.5rem;
  color: var(--color-red);
  font-weight: 700;
  margin-top: 0.5rem;
}

.header-mobile__footer-under {
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-mobile__footer-socials {
  display: flex;
  gap: 0.25rem;
}

.header-mobile__footer-social {
  padding: 0.25rem;
}
.header-mobile__footer-social img {
  display: block;
}

.header-mobile__footer-switch-icon {
  display: block;
}

.hero {
  position: relative;
  min-height: 85dvh;
  display: flex;
  align-items: center;
}
@media (max-width: 480px) {
  .hero {
    min-height: 85dvh;
  }
}
.hero::before {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  width: 100%;
  height: 10rem;
  transform: rotate(180deg);
  background: linear-gradient(180deg, #f2f2f2 0%, rgba(242, 242, 242, 0) 100%);
  pointer-events: none;
}

.hero__container {
  flex: 1;
}

.hero__content {
  position: relative;
  z-index: 1;
  max-width: 41.25rem;
  color: var(--color-black);
}

.hero__title {
  font-size: 8rem;
  font-weight: 700;
}
@media (max-width: 1024px) {
  .hero__title {
    font-size: 6rem;
  }
}
@media (max-width: 480px) {
  .hero__title {
    font-size: 4rem;
  }
}

.hero__subtitle {
  font-size: 1.5rem;
  font-family: var(--font-pt);
  margin-top: 1.5rem;
}
@media (max-width: 480px) {
  .hero__subtitle {
    font-size: 1.125rem;
  }
}

.hero__button {
  margin-top: 2.5rem;
  padding: 1rem 2rem;
}

.hero__picture {
  position: absolute;
  inset: 0;
}
.hero__picture img {
  height: 100%;
  width: 100%;
}
@media (max-width: 480px) {
  .hero__picture img {
    object-position: 35% center;
  }
}

.loop-items {
  padding: 0.125rem 0;
  overflow: hidden;
  position: relative;
}
@media (max-width: 480px) {
  .loop-items {
    padding: 0;
  }
}
.loop-items::before, .loop-items::after {
  content: "";
  position: absolute;
  top: 0;
  width: 17.5rem;
  height: 100%;
  background: linear-gradient(90deg, #f2f2f2 0%, rgba(242, 242, 242, 0) 100%);
  z-index: 2;
  pointer-events: none;
}
@media (max-width: 480px) {
  .loop-items::before, .loop-items::after {
    content: none;
  }
}
.loop-items::before {
  left: 0;
}
.loop-items::after {
  right: 0;
  transform: rotate(180deg);
}

.loop-items__list {
  display: flex;
  gap: 0.75rem;
  transition-timing-function: linear !important;
}
@media (max-width: 480px) {
  .loop-items__list {
    gap: 0.25rem;
  }
}

.loop-items__item {
  width: auto;
  flex-shrink: 0;
  border-radius: 1rem;
  background-color: rgba(41, 41, 41, 0.1);
}

.loop-items__link {
  width: 7rem;
  height: 7rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease-in-out;
}
@media (max-width: 768px) {
  .loop-items__link {
    width: 5rem;
    height: 5rem;
  }
}
@media (max-width: 480px) {
  .loop-items__link {
    width: 2.5rem;
    height: 2.5rem;
    padding: 0.5rem;
    border-radius: 0.5rem;
  }
  .loop-items__link img {
    height: 100%;
    object-fit: contain;
  }
}
.loop-items__link:hover {
  transform: scale(1.5);
}

@keyframes loop {
  0% {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
.main-about {
  margin-top: 10rem;
}
@media (max-width: 480px) {
  .main-about {
    margin-top: 5rem;
  }
}

.main-about__subtitle {
  color: var(--silver-color);
  font-size: 1.5rem;
  font-weight: 700;
  font-family: var(--font-pt);
}
@media (max-width: 480px) {
  .main-about__subtitle {
    font-size: 1.25rem;
  }
}

.main-about__title {
  margin-top: 2.5rem;
}
@media (max-width: 480px) {
  .main-about__title {
    font-size: 2rem;
    margin-top: 1rem;
  }
}

.main-about__text {
  margin-top: 1rem;
  font-size: 1.125rem;
  font-family: var(--font-pt);
}

.main-about__button {
  margin-top: 2.5rem;
}
@media (max-width: 480px) {
  .main-about__button {
    width: 100%;
    text-align: center;
  }
}

.main-about__items {
  display: flex;
  align-items: flex-end;
  gap: 2.5rem;
}
@media (max-width: 480px) {
  .main-about__items {
    gap: 0.5rem;
    margin-top: 3.5rem;
  }
}

.main-about__item {
  position: relative;
  flex: 1;
  display: flex;
  justify-content: center;
  padding: 0 0.875rem;
}
.main-about__item::before {
  content: "";
  pointer-events: none;
  background: linear-gradient(180deg, rgba(175, 30, 40, 0.1) 0%, rgba(242, 242, 242, 0.1) 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 1.5rem 1.5rem 0 0;
  animation: flash 3s ease-in-out infinite;
}
.main-about__item:first-child {
  min-height: 12rem;
}
@media (max-width: 480px) {
  .main-about__item:first-child {
    min-height: 7rem;
  }
}
.main-about__item:nth-child(2) {
  min-height: 252px;
}
@media (max-width: 480px) {
  .main-about__item:nth-child(2) {
    min-height: 8.625rem;
  }
}
.main-about__item:last-child {
  min-height: 19.5625rem;
}
@media (max-width: 480px) {
  .main-about__item:last-child {
    min-height: 10.1875rem;
  }
}
.main-about__item:hover .main-about__image {
  transform: translateY(-1.25rem) scale(1.1);
}

.main-about__image {
  position: relative;
  margin-top: -3.125rem;
  transition: transform 0.3s ease-in-out;
}
@media (max-width: 480px) {
  .main-about__image {
    margin-top: -2rem;
  }
}

.main-gallery {
  position: relative;
  margin-top: 7.5rem;
  overflow: hidden;
}

.main-gallery__head {
  display: flex;
  justify-content: space-between;
  margin-bottom: 3.5rem;
}
@media (max-width: 480px) {
  .main-gallery__head {
    margin-bottom: 2rem;
  }
}

